*{
  margin: 0;
  padding: 0;
}
*::-webkit-scrollbar{
  width: 0px;
}
body{
  overflow-x: hidden;
}
button{
  all: unset;
  cursor: pointer;
}

input[type="checkbox"] {
  display: none;
}
input::placeholder{
  color: #fffafa;
}
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;

}
input[type=number]{
 -moz-appearance: textfield;
 appearance: textfield;
}
li{
  list-style: none;
}
/* Dark theme */
.dark{
  background-image: url("https://i.pinimg.com/originals/85/d4/39/85d439a34e50297e016a7621745ef6ca.gif");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  backdrop-filter: blur(1vh);
  color: #fff;
  height: 110vh;
}
body{
  background-image: url("https://i.pinimg.com/originals/e8/60/78/e860781951c4bcbbba38083566fc6fdd.gif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  backdrop-filter: blur(1vh);
  height: 110vh;
}
#Corpo{
  border: 0.2vh solid transparent;
  height: 100vh;
  overflow: auto;
}
#DigiImg{
  border: 0.2vh solid transparent;
  width: 99%;
  height: 60vh;
}
#DigimonName,#NomePoke{
  text-align: center;
  font-size: 6vh;
  color: black;
  text-shadow: 0.2vh 0.2vh 0.2vh yellow;
}
#DigimonName{
  color: #0F4480;
  font-size: 4vh;
  text-shadow: 0.2vh 0.2vh 0.2vh #EEC640;
}
#imagePokemon{
  width: 45vh;
  margin-top: 2vh;
  border: 0.2vh solid transparent;
  border-radius: 50%;
  background-image: url("https://www.pngarts.com/files/4/Pokeball-Transparent.png");
  background-position: center;
  background-size: cover;

}
#NomePoke{
  color: #3B4CCA;
  font-size: 3vh;
  text-shadow: 0.2vh 0.2vh 0.2vh #FFDE00;
}
