/* Sidebar e Header */
.Sidebar{
    display: none;
    border: 0.2vh solid black;
    position: absolute;
    width: 30vh;
    height: 100%;
    background-image: url("https://i.pinimg.com/originals/d4/82/2f/d4822f536e1625c8482ccb460c9fbcf4.gif");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
}
.Sidebar > a{
    display: block;
    text-align: center;
    color: rgb(255, 255, 14);
    font-size: 4.5vh;
    text-decoration: none;
    margin-top: 2.5vh;
    text-shadow: 0.2vh 0.2vh 0.4vh rgb(255, 255, 14);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-weight: bold;
}
.Sidebar > a:hover{
    color: #0ff;
}
.Sidebar > h1{
    font-size: 4vh;
    text-align: center;
    color: red;
    font-style: italic;
    word-break: break-all;
    text-shadow: 0.1vh 0.1vh 0.1vh whitesmoke;
    border: 0.2vh solid transparent;
}
.Sidebar > img{
    display: block;
    margin: auto;
    border: 0.1vh solid transparent;
    border-radius: 50%;
    width: 10vh;
    height: 10vh;
    margin: auto;
    margin-top: -5.0vh;
}
.Entrar > img{
    width: 6vh;
    margin-top: -0.6vh;
    position: absolute;
}
.Sair{
    width: 100%;
}
.Sair  > img{
    display: block;
    border: 0.2vh solid transparent;
    margin-left: auto;
    margin-right: 2vh;
    width: 7vh;
}
.Header{
    border: 0.2vh solid transparent;
    background: linear-gradient(135deg , rgba(255,255,255,0.1),rgba(255,255,255,0));
    backdrop-filter: blur(1vh);
    -webkit-backdrop-filter: blur(8vh);
    color: white;
    height: 12vh;
}
.Theme{
    position: absolute;
    margin-left: 190vh;
}
.Theme > button > img{
    width: 9vh;
    cursor: pointer;
    margin-top: 1vh;
    margin-left: -2vh;
}
.HeaderText > button{
    margin-left: 4vh;
    margin-top: 3vh;
    position: absolute;
}
.HeaderText > span {
    font-size: 8vh;
    margin-left: 12vh;
    position: absolute;
    margin-top: 0.7vh;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-shadow: 0.2vh 0.2vh 0.2vh purple;
    color: yellow;
}
/* Cores */
.Cores{
    border: 0.2vh solid white;
    height: 20vh;
    width: 20vh;
}
.TextInput{
    display: none;
}
/* Bola */
.Bola{
    position: absolute;
    top: calc(50% - 40vh);
    left: calc(50% - 40vh);
    width: 80vh;
    height: 80vh;
    border-radius: 50vh;
    box-shadow:
    inset 0 0 6vh #fff,
    inset 3vh 0 9vh #f0f,
    inset -3vh 0 9vh #0ff,
    inset 3vh 0 31vh #f0f,
    inset -3vh 0 31vh #0ff,
    0 0 6vh #fff,
    -2vh 0 9vh #f0f,
    2vh 0 9vh #0ff;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    animation: Galaxy 15s ease-in infinite ;
}
@keyframes Galaxy{
    0%{
        background-image: url("https://media.tenor.com/xfsFj7A6lJMAAAAd/galaxy.gif");
        transform: rotate(0deg);
    }
    10%{
        background-image: url("https://i.pinimg.com/originals/d2/c2/58/d2c258321507ba683c80747d8888e322.gif");
        transform: rotate(25deg);
    }
    20%{
        background-image: url("https://media.tenor.com/P2z3N1InIlgAAAAM/galaxy.gif");
        transform: rotate(75deg);
    }
    30%{
        background-image: url("https://i.pinimg.com/originals/92/ac/36/92ac3696dee29e29e147826267abf6e8.gif");
    }
    40%{
        background-image: url("https://i.pinimg.com/originals/d2/c2/58/d2c258321507ba683c80747d8888e322.gif");
        transform: rotate(125deg);
    }
    50%{
        background-image: url("https://cdn.pixabay.com/animation/2022/11/16/14/56/14-56-49-778_512.gif");
        transform: rotate(175deg);

    }
    60%{
        background-image: url("https://media.tenor.com/iIgQMUqXbUQAAAAd/gurren-lagann.gif");
        transform: rotate(185deg);
    }
    70%{
        background-image: url("https://media.tenor.com/lgUwFn_j0p8AAAAd/galaxy-space.gif");
        transform: rotate(200deg);
    }
    80%{
        background-image: url("https://64.media.tumblr.com/b446d52ce83abbc4f9823c21ddb07a55/tumblr_ok9tviEV6r1v1fssvo1_500.gif");
        transform: rotate(215deg);
    }
    90%{
        background-image: url("https://i.gifer.com/Td3O.gif");
        transform: rotate(235deg);
    }
    100%{
        background-image: url("https://thumbs.gfycat.com/PoisedSlipperyAdeliepenguin-max-1mb.gif");
        transform: rotate(250deg);
    }
}
/* Imagem Pdf */
.IMGPDF > img{
    display: block;
    margin: auto;
    margin-top: 2vh;
    width: 80%;
    height: 80vh;
    border: 0.2vh solid transparent;
    border-radius: 2vh;
}
.FormControl{
    display: block;
    margin: auto;
    border: 0.2vh solid transparent;
    width: 20%;
    margin-top: 2vh;
}
.FormControl > input{
    font-size: 2vh;
}
.ButtnGenerator{
    display: block;
    border: 0.2vh solid #ffffff;
    background-color: #5692c4;
    width: 35vh;
    border-radius: 20vh;
    color: #ffffff;
    margin: auto;
    margin-top: 2vh;
    font-size: 4vh;
    text-align: center;
    cursor: pointer;
    transition: .4s;
}
.ButtnGenerator:hover{
    filter: invert(1);
}
/* TODO List */
.LIST,.LISTM{
    border: 0.5vh solid yellow;
    width: 204vh;
    background: #0f292c;
    color: #ffffff;
    border-radius: 0 0 2vh 2vh;
    height: 26vh;
}
.LIST > h1,.LISTM > h1{
    text-align: center;
    font-size: 7vh;
    color: rgb(255, 251, 0);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-shadow: 0.2vh 0.2vh 0.2vh #000000;
}
.LIST > textarea::placeholder,.LISTM > input{
    color: #ffffff;
}
.LIST > textarea,.LISTM > input{
    all: unset;
    text-align: center;
    font-size: 4vh;
    color: #ffffff;
    display: block;
    margin: auto;
    border: 0.1vh solid transparent;
    width: 100%;
    word-wrap: break-word;
    height: 10vh;
}
.LIST > button,.LISTM > button{
    display: block;
    margin: auto;
    border: 0.5vh solid #ffffff;
    background: rgb(23, 49, 167);
    color: #1df7ff;
    width: 20vh;
    font-size: 3vh;
    border-radius: 50vh;
    text-align: center;
    margin-top: 1vh;
}
.LIST > button:hover,.LISTM > button:hover{
    filter: invert(1);
}
.ListaContainer{
    border: 0.1vh solid transparent;
    width: 95%;
    height: 80vh;
    margin: auto;
    display: block;
    overflow: auto;
    border-radius: 2vh;
}
.ListaContainer::-webkit-scrollbar{
    width: 0px;
}
.ListaCO,.ListaCOI{
    background: black;
    border: 0.4vh solid yellow;
    width: 45vh;
    height: 25vh;
    display: inline-block;
    overflow: auto;
    margin-left: 2vh;
    text-align: justify;
    border-radius: 2vh;
}
.ListaCO > h1{
    color: #ffffff;
    word-wrap: break-word;
    font-size: 3vh;
    width: 80%;
    display: block;
    margin: auto;
    margin-top: 1vh;
    border: 0.1vh solid transparent;
}
.ListaCO > h1:hover{
    text-decoration: line-through;
}
.ButtonLis{
    display: block;
    margin: auto;
    border: 0.4vh solid #f0f;
    background: #5692c4;
    color: #ffffff;
    width: 80%;
    text-align: center;
    border-radius: 2vh;
    font-size: 2vh;
    margin-top: 2vh;
}
.ButtonLis:hover{
    filter: invert(1);
}
/* Mural */
.LISTM{
    height: 38vh;
}
.ListaCOI{
    height: 76vh;
