    height: 97vh;
    border-radius: 2vh;
}

.DigiBody > input{
    all: unset;
    font-size: 4vh;
    display: block;
    width: 100%;
    text-align: center;
    color: #741474;
    background: #e97427;
}
.ClickPo,.PQ{
    display: block;
    margin: auto;
    margin-top: 1vh;
    text-align: center;
    font-size: 4vh;
    border: 0.3vh solid black;
    border-radius: 50vh;
    width: 80%;
    background: #5692c4;
}
.ClickPo:hover{
    filter: invert(1);
}
.PQ{
    background: #B3A125;
    height: 6vh;
    color: #3B4CCA;
    transition: .2s;
}
.PQ:hover{
    background: #FFDE00;
    color: #374add;
}
/* Home */
.Home{
    width: 80%;
    text-align: center;
    margin: auto;
    display: block;
    margin-top: 4vh;
    background: linear-gradient(135deg , rgba(255,255,255,0.1),rgba(255,255,255,0));

    -webkit-backdrop-filter: blur(8vh);
    border-radius: 2vh;
}
.Home > h2{
    color: transparent;
    background-image: url("https://media.tenor.com/XAJd_LyABMkAAAAC/galaxy-stars.gif");
    -webkit-background-clip: text;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    font-size: 20vh;
}
.Home > p{
    color: purple;
    text-shadow: 0.2vh 0.2vh 0.4vh purple;
    font-size: 5vh;
    display: block;
    margin: auto;
    text-align: justify;
    margin-top: 1vh;
    width: 80%;
}
/* Cores */
.Cores{
    border: 0.2vh solid transparent;
    width: 180vh;
    margin: auto;
    display: block;
    margin-top: 3vh;
}
.Cores > button,.Register > button,.Login > button,.Começar{
    display: block;
    margin: auto;
    font-size: 4vh;
    color: rgb(255, 251, 0);
    background: #690869;
    border: 0.4vh solid #f0f;
    margin-top: 2vh;
    width: 50vh;
    text-align: center;
    border-radius: 50vh;
}
.Cores > div,.Register > button{
    border: 0.2vh solid transparent;
    margin-top: 2vh;
}
.Register > button:hover{
    filter: invert(1);
}
.Cores > button:hover,.Cores > div > h1:hover,.Login > button,.Começar:hover{
    filter: invert(1);
}
.Cores > div > h1{
    display: inline-block;
    border: 0.2vh solid transparent;
    width: 25vh;
    text-align: center;
    font-size: 5vh;
    color: black;
    text-shadow: 0.2vh 0.2vh 0.2vh white;
    cursor: pointer;
}
/* Register */
.Register{
    border: 0.2vh solid transparent;
    width: 80%;
    margin: auto;
    display: block;
    margin-top: 10%;
}
.Register > input{
    width: 60%;
    display: block;
    margin: auto;
    margin-top: 2vh;
    height: 6vh;
    background: #5692c4;
    border-radius: 50vh;
    text-align: center;
    border: 0;
    font-size: 3vh;
    color: snow;
}
.Login{
    display: block;
    margin: auto;
    border: 0.4vh solid transparent;
    width: 90vh;
 background: linear-gradient(135deg , rgba(255,255,255,0.1),rgba(255,255,255,0));
 backdrop-filter: blur(1vh);
 -webkit-backdrop-filter: blur(8vh);
 height: 100%;
 margin-top: 3vh;
 border-radius: 10vh;
}
.Login > img{
    display: block;
    margin: auto;
    border-radius: 50%;
    width: 50vh;
    height: 50vh;
    margin-top: 3vh;
}
.Login > h1{
    color: yellow;
    margin-top: 1vh;
    text-align: center;
    font-size: 10vh;
    text-shadow: 0.2vh 0.2vh 0.2vh #11101D;
    word-wrap: break-word;
}
/* Pokedex */
.Pokedex{
    border: 0.2vh solid transparent;
